* {
    box-sizing: border-box;
    margin:0;
    padding:0;
}
input:focus,button:focus{
    outline: none;
}
button{
    cursor: pointer;
}
a {
    text-decoration: none;
    color: inherit;

}
:root{

    /* colors */
    --light-primary-text:#FCFCFC;
    --dark-primary-text:#1E3231;
    --dark-button:#485665;
    --light-button:#D0A5C0;
    --light-background:#F7F7FF;
    --dark-background:#495867;

    /* width & height */
    --app-max-width:1000px;
    --nav-height:60px;

}
.facebook-login-icon{
    display: inline-block;
    background-color: #0078FF;
    color:white;
}


.button{
    border:none;
    border-radius: 4px;
    padding:9px 15px;
    font-weight: bold;
    transition: all .2s;
    white-space: nowrap;
    cursor: pointer;
    min-width: 60px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 1px 14px;
  
   
}
@media screen and (hover:hover){
    .button:hover{
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 14px;
        
    }
   }
.button:focus{
    outline: none;
}
.bg-pink{
    background-color: #D0A5C0;
    background-color: var(--light-button);
}
.react-thumbnail-generator{
    width:100%;
    height:100%;
    overflow: hidden;
    
}
.react-thumbnail-generator img{
    width:100%
}
